			],
			modeIndex: -1,
			styleIndex: -1,
			current: 0,
			mode: 'default',
			dotsStyles: {}
		}
	},
	onLoad() {},
	methods: {
		change(e) {
			this.current = e.detail.current
		},
		selectStyle(index) {
			this.dotsStyles = this.dotStyle[index]
			this.styleIndex = index
		},
		selectMode(mode, index) {
			this.mode = mode
			this.modeIndex = index
			this.styleIndex = -1
			this.dotsStyles = this.dotStyle[0]
		}
	}
}
